// @require '../../style/reset.scss'

import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

const dateDefaultMonth = new Date(2016, 6, 1);

const Demo = function () {
  return (
    <div>
      <h2>Calendar Default View Month</h2>
      <Datepicker
        placeholder={'点击选择日期'}
        initMonth={dateDefaultMonth}
      />
    </div>
  );
};

ReactDom.render(
  <Demo />,
  document.getElementById('demo5')
);
